/* eslint-disable linebreak-style */
/* eslint-disable react/jsx-indent */

import React from 'react'
import { Switch, Redirect, Route } from 'react-router-dom'

/** Authentication */
import Login from '../components/admin/Authentication/Login.jsx'
import Register from '../components/admin/Authentication/Register.jsx'
import Forgot from '../components/admin/Authentication/ForgetPassword.jsx'

import Dashboard from '../views/Dashboard'
import ManageRolesPermissions from '../views/ManageRolesPermissions'
import RoleDetails from '../views/ManageRolesPermissions/RoleDetails'
import AddRoles from '../views/ManageRolesPermissions/AddRoles'
import ManageUsers from '../views/ManageUsers'
import UserDetails from '../views/ManageUsers/UserDetails'
import AddUser from '../views/ManageUsers/AddUser'
import ManageB2BUsers from '../views/ManageB2BUsers'
import B2BUserDetails from '../views/ManageB2BUsers/UserDetails'
import B2BAddUser from '../views/ManageB2BUsers/AddUser'
import B2BSubUserDetails from '../views/ManageB2BUsers/SubUserDetails'
import B2BUserRequests from '../views/ManageB2BUsers/UserRequests'
import B2BUserRequestDetail from '../views/ManageB2BUsers/UserRequests/RequestDetail'
import B2BPackageDeposits from '../views/ManageB2BUsers/PackageDeposits'
import B2BPackageDepositsDetails from '../views/ManageB2BUsers/PackageDeposits/DepositDetails'
import B2BAddPackageDeposits from '../views/ManageB2BUsers/PackageDeposits/AddPackage'
import ManageArtists from '../views/ManageArtists'
import AddArtist from '../views/ManageArtists/AddArtist'
import ArtistDetail from '../views/ManageArtists/ArtistDetail'
import ArtistProjects from '../views/ManageArtists/ProjectsArtwork'
import ArtistPricing from '../views/ManageArtists/ManagePricing'
import AddPricing from '../views/ManageArtists/AddPricing'
import EditPricing from '../views/ManageArtists/EditPricing'
import ProjectDetails from '../views/ManageArtists/ProjectArtworkDetails'
import ProjectReview from '../views/ManageArtists/ProjectReview'
import ProjectReviewDetails from '../views/ManageArtists/ProjectReviewDetails'
import ContactArtist from '../views/ManageArtists/ContactArtist'
import ArtistPayout from '../views/ManageArtists/ArtistPayout'
import B2CUserOrders from '../views/ManageOrders/B2COrders'
import B2CUserOrderDetail from '../views/ManageOrders/B2COrders/B2COrderDetails'
import B2BUserOrders from '../views/ManageOrders/B2BOrders'
import B2BUserOrderDetail from '../views/ManageOrders/B2BOrders/B2BOrderDetails'
import B2BSubUserOrderDetail from '../views/ManageOrders/B2BOrders/B2BSubOrderDetails'
import Invoice from '../views/ManageOrders/Invoice'
import InvoiceDetail from '../views/ManageOrders/Invoice/InvoiceDetail'


import PublicRoutes from './PublicRoutes'
// import PrivateRoutes from './PrivateRoutes'

const Routes = () => (
<Switch>
  <PublicRoutes path={`${process.env.PUBLIC_URL}/login`} component={Login} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/register`} component={Register} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/forgot`} component={Forgot} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/dashboard`} component={Dashboard} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-roles-permissions`} component={ManageRolesPermissions} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-roles-permissions/role-details`} component={RoleDetails} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-roles-permissions/add-roles`} component={AddRoles} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-users`} component={ManageUsers} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-users/user-details`} component={UserDetails} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-users/add-user`} component={AddUser} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-b2b-users`} component={ManageB2BUsers} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-b2b-users/user-details`} component={B2BUserDetails} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-b2b-users/add-user`} component={B2BAddUser} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-b2b-users/sub-user-details`} component={B2BSubUserDetails} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-b2b-users/user-requests`} component={B2BUserRequests} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-b2b-users/request-detail`} component={B2BUserRequestDetail} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-b2b-users/package-deposits`} component={B2BPackageDeposits} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-b2b-users/deposit-details`} component={B2BPackageDepositsDetails} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-b2b-users/add-package`} component={B2BAddPackageDeposits} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists`} component={ManageArtists} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/add-artist`} component={AddArtist} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/artist-detail`} component={ArtistDetail} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/artist-projects`} component={ArtistProjects} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/artist-pricing`} component={ArtistPricing} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/add-pricing`} component={AddPricing} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/edit-pricing`} component={EditPricing} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/project-details`} component={ProjectDetails} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/project-review`} component={ProjectReview} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/project-review-details`} component={ProjectReviewDetails} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/contact-artist`} component={ContactArtist} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-artists/artist-payout`} component={ArtistPayout} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-orders/b2cuser-orders`} component={B2CUserOrders} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-orders/b2cuser-order-detail`} component={B2CUserOrderDetail} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-orders/b2buser-orders`} component={B2BUserOrders} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-orders/b2buser-order-detail`} component={B2BUserOrderDetail} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-orders/b2bsub-user-order-detail`} component={B2BSubUserOrderDetail} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-orders/invoice`} component={Invoice} exact />
  <PublicRoutes path={`${process.env.PUBLIC_URL}/manage-orders/invoice-detail`} component={InvoiceDetail} exact />
  
   
  <Redirect path={`${process.env.PUBLIC_URL}/`} to={`${process.env.PUBLIC_URL}/dashboard`} />
</Switch>
)

export default Routes
