import React, { Component } from "react";
import { withRouter, Link } from "react-router-dom";
import MetisMenu from "metismenujs";

import SimpleBar from "simplebar-react";

/** icon images */
import dashboard from '../../assets/images/admin/dashboard-icon.svg'
import roles from '../../assets/images/admin/manage-roles-icon.svg'
import users from '../../assets/images/admin/manage-users-icon.svg'
import b2b_users from '../../assets/images/admin/manage-b2b-users-icon.svg'
import artists from '../../assets/images/admin/manage-artists-icon.svg'
import products from '../../assets/images/admin/manage-products-icon.svg'
import orders from '../../assets/images/admin/manage-orders-icon.svg'
import support from '../../assets/images/admin/manage-support-icon.svg'
import reports from '../../assets/images/admin/reports-icon.svg'
import marketing from '../../assets/images/admin/marketing-icon.svg'
import cms from '../../assets/images/admin/cms-icon.svg'
import settings from '../../assets/images/admin/settings-icon.svg'

