import React, {useState} from 'react'
import { Col, Row, Form, Input, FormGroup, Button } from 'reactstrap';


function AddPricingContent(props) {
    {/* Match Url */ }
    const { url } = props.match
    const showAddPricing = [`${process.env.PUBLIC_URL}/manage-artists/add-pricing`].indexOf(url) > -1;
    if(showAddPricing){
        document.body.classList.add('bg-secondary','add-user');
    }
    return (
        <>
            <div className="main-title">
                <h1>Add Pricing</h1>
            </div>
            <Form>
                <Row>
                    <Col md="6">
                        <FormGroup>
                            <label>CATEGORY</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>RETAIL PRICE</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>MARGIN</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>MARKUP</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                </Row>
                <div className="text-center">
                    <Button type="submit" className="common-btn color-blue">SUBMIT</Button>
                </div>
            </Form>
        </>
    )
}

export default AddPricingContent
